import * as React from 'react';
import { styled, createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import MuiDrawer from '@mui/material/Drawer';
import Box from '@mui/material/Box';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Badge from '@mui/material/Badge';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';

import {useGun} from '../hooks/useGun';
import { MainListItems } from './Components/Listitems';
import Copyright from './Components/Copyright';
import GunChat from './Components/GunChat';

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    '& .MuiDrawer-paper': {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidth,
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
      boxSizing: 'border-box',
      ...(!open && {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
          easing: theme.transitions.easing.sharp,
          duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
          width: theme.spacing(9),
        },
      }),
    },
  }),
);

const mdTheme = createTheme();

function DashboardContent({user}) {
  	const [open, setOpen] = React.useState(true);
  	const toggleDrawer = () => {
    	setOpen(!open);
  	};

  	return (
    	<ThemeProvider theme={mdTheme}>
      		<Box sx={{ display: 'flex' }}>
        		<CssBaseline />
        		<AppBar position="absolute" open={open}>
          			<Toolbar sx={{ pr: '24px', /* keep right padding when drawer closed */}}>
            			<IconButton
              				edge="start" color="inherit"
              				aria-label="open drawer" onClick={toggleDrawer}
              				sx={{
                				marginRight: '36px',
                				...(open && { display: 'none' }),
              				}}
            			>
              				<MenuIcon />
            			</IconButton>
            			<Typography component="h1" variant="h6" color="inherit" noWrap sx={{ flexGrow: 1 }}>
              				Dashboard
            			</Typography>
						<IconButton color="inherit" onClick={(e)=>{}}>
			  				<AddCircleOutlineIcon/>
						</IconButton>
            			<IconButton color="inherit">
              				<Badge badgeContent={4} color="secondary">
                				<NotificationsIcon />
              				</Badge>
            			</IconButton>
          			</Toolbar>
        		</AppBar>
        		<Drawer variant="permanent" open={open}>
          			<Toolbar
            			sx={{
              			display: 'flex',
              			alignItems: 'center',
              			justifyContent: 'flex-end',
              			px: [1],
            			}}
          			>
            			<IconButton onClick={toggleDrawer}>
              				<ChevronLeftIcon />
            			</IconButton>
          			</Toolbar>
          			<Divider />
          			<List component="nav">
            			<MainListItems/>
          			</List>
        		</Drawer>
        		<Box component="main" sx={{ backgroundColor: (theme) =>
        	      		theme.palette.mode === 'light'
        	        	? theme.palette.grey[100]
        	        	: theme.palette.grey[900],
        	    	flexGrow: 1,
        	    	height: '100vh',
        	    	overflow: 'auto',
        	  		}}
        		>
        	  		<Container maxWidth="lg" sx={{ mt: 12, mb: 4 }}>
		  				<Paper sx={{mt: 4, mb: 4}}>
							{!!user.is ? <GunChat user={user}/> : <h2>Chat</h2>}
						</Paper>
            			<Copyright sx={{ pt: 4 }} />
        	  		</Container>
        		</Box>
      		</Box>
    	</ThemeProvider>
	);
}

export default function Dashboard() {
	const gun = useGun();
	const user = gun.user();
  return <DashboardContent user={user} />;
}